@forward 'globals';

.profile-container {
    margin-top: 1rem;
    background-color: #EEE;
    border-radius: 20px;
    overflow: hidden;
}

.wrapper {

    form {
        gap: 0;
    }

    input {
        border-top-right-radius: 0;
        border-bottom-right-radius: 0;
        flex: 1;
    }

    .cta {
        background-color: black;
        color: white;
        border-bottom-left-radius: 0;
        border-top-left-radius: 0;
    }
}

.profile-wrapper {
    position: relative;
    width: 25rem;
    padding-block: 2rem;
    margin-inline: 2rem;

    &:not(:last-of-type) {
        border-bottom: 1px solid #DDD;
    }

    button {
        position: absolute;
        right: 0;
        top: 2rem;
        background: none;
        color: blue;
        padding: 0;
    }

    .profile-img {
        width: 5rem;
        height: 5rem;
        margin-bottom: 1rem;
        border: 1px solid white;
    
        &, img {
            border-radius: 50%;
        }
    }

    .bio {
        margin-top: 1rem;
    }
}