* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  display: grid;
  place-content: center;
  min-height: 100vh;
}

img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}

button, input {
  border: 1px solid #E6E6E6;
  border-radius: 10px;
  outline: none;
  padding: 0.5rem 1rem;
  font: inherit;
}

button {
  background-color: #DDD;
  border-color: transparent;
  cursor: pointer;
}

.flex {
  display: flex;
  gap: 2rem;
}

.profile-container {
  margin-top: 1rem;
  background-color: #EEE;
  border-radius: 20px;
  overflow: hidden;
}

.wrapper form {
  gap: 0;
}
.wrapper input {
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
  flex: 1;
}
.wrapper .cta {
  background-color: black;
  color: white;
  border-bottom-left-radius: 0;
  border-top-left-radius: 0;
}

.profile-wrapper {
  position: relative;
  width: 25rem;
  padding-block: 2rem;
  margin-inline: 2rem;
}
.profile-wrapper:not(:last-of-type) {
  border-bottom: 1px solid #DDD;
}
.profile-wrapper button {
  position: absolute;
  right: 0;
  top: 2rem;
  background: none;
  color: blue;
  padding: 0;
}
.profile-wrapper .profile-img {
  width: 5rem;
  height: 5rem;
  margin-bottom: 1rem;
  border: 1px solid white;
}
.profile-wrapper .profile-img, .profile-wrapper .profile-img img {
  border-radius: 50%;
}
.profile-wrapper .bio {
  margin-top: 1rem;
}

